// let lookSet = []

var sourceType = [
	['camera'],
	['album'],
	['camera', 'album']
]
var sizeType = [
	['compressed'],
	['original'],
	['compressed', 'original']
]
import uniPopup from '../../components/uni-popup/uni-popup.vue'
import uniNavBar from '../../components/uni-nav-bar/uni-nav-bar.vue';
export default {
	components: {
		uniNavBar,
		uniPopup
	},
	data() {
		return {
			barTitles:['所有人可见',"仅自己可见"],
			barTitle:'所有人可见',
			textValue:'',//文本域值
			imageList: [],
			sourceTypeIndex: 2,
			sourceType: ['拍照', '相册', '拍照或相册'],
			sizeTypeIndex: 2,
			sizeType: ['压缩', '原图', '压缩或原图'],
			countIndex: 8,
			count: [1, 2, 3, 4, 5, 6, 7, 8, 9],
			showpopup:true ,//默认打开弹出框
			isget: false //标识返回
			}
	},
	onBackPress(){
		// 沒有內容和图片直接返回
		if(!this.textValue && this.imageList.length < 1){ return;}
		if(!this.isget){
			this.saveDraft()	
		}
		return true;
	},
	methods: {
		// 返回
		back() {
			console.log('back')
			uni.navigateBack({
				delta: 1
			})
		},
		// 返回保存草稿
		saveDraft(){
			uni.showModal({
				content: '是否保存为草稿?',
				cancelText: '不保存',
				confirmText: '保存',
				success: res => {
					if(res.confirm){
						console.log("保存")
					}else{
						console.log("不保存")
					}
					this.isget = true;
					// uni.navigateBack({
					// 	delta: 1
					// })
					uni.switchTab({
					    url: '/pages/comment/comment'
					});
				}
			});
		},
		post() {},
		// 设值可看隐私
		privacy(){
			uni.showActionSheet({
				itemList: this.barTitles,
				success: res =>{ 
					let index = res.tapIndex;
					this.barTitle = this.barTitles[index]
					} 
			})
			
		},
		chooseImage: async function() {
			if (this.imageList.length === 9) {		
					return;	
			}
			uni.chooseImage({
				sourceType: sourceType[this.sourceTypeIndex],
				sizeType: sizeType[this.sizeTypeIndex],
				count: this.imageList.length + this.count[this.countIndex] > 9 ? 9 - this.imageList.length : this.count[this.countIndex],
				success: (res) => {
					this.imageList = this.imageList.concat(res.tempFilePaths);
					// this.$emit('uploud',this.imageList)封装成组件子传父数据
				},
					})
		},
		previewImage: function(e) {
			var current = e.target.dataset.src
			uni.previewImage({
				current: current,
				urls: this.imageList
			})
		},
		// 删图
		deteleImg(index){
			uni.showModal({
				title: '提示',
				content: '是否要删除该图片',
				success: (res)=> {
					if (res.confirm) {
						this.imageList.splice(index,1);
						// this.$emit('uploud',this.imageList)封装成组件子传父数据 父组件方法接受数据赋给父imageList
					}
				}
			})
		},
		// 隐藏弹出框
		hidePopup(){
			this.showpopup=false;
		}
			

	}
}
